import React from 'react';
import {Map, InfoWindow, Marker, Polygon, GoogleApiWrapper} from 'google-maps-react';



export class SimpleMap extends React.Component {    
  constructor(props) {
    super(props);
    this.state = {
      showingInfoWindow: false,
      activeMarker: {},
      selectedPlace: {},
      center: {
          lat: 29.850002,  lng: 121.589764
      }
    }
    
    // binding this to event-handler functions
    // this.onMarkerClick = this.onMarkerClick.bind(this);
    // this.onMapClicked = this.onMapClicked.bind(this);
  }

  onMarkerClick = (props, marker, e) => {
    console.log('this is:', props);
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });
  }

  onMapClicked = () => {
    console.log('this is:', this);
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      })
    }
  }

  onChangeMapCenter = (point) => {
    this.props.changeMapCenter(point);
  }

  render() {
     
    var triangleCoords = [
      {lat: 29.889557, lng:  121.584567},
      {lat: 29.842866, lng:  121.541551},
      {lat: 29.851755, lng:  121.621809}
    ];
    return(
      <Map google={this.props.google} 
          zoom={14} 
          onClick={this.onMapClicked} 
          initialCenter={this.state.center}
          style={{width: '100%', height: '100%', position: 'relative'}}
          className={'map'} >
          <Marker
            title={'The marker`s title will appear as a tooltip.'}
            name={'SOMA'}
            position={this.state.center} />
          <Polygon
            paths={triangleCoords}
            strokeColor="#FF00FF"
            strokeOpacity={0.8}
            strokeWeight={2}
            fillColor="#0000FF"
            fillOpacity={0.35} />
      </Map>
    )
  }
}

export default GoogleApiWrapper({
  apiKey: ('AIzaSyCkVtTOEli1pN0_OE3N8jMn-p6RccuWi7I')
})(SimpleMap)



